<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 150px)"></QueryBox>
      <div class="queryBtns">
        <Button type="success" size="small" style="margin-right: 10px" class="btn-add" icon="md-add" @click.stop="openAddModal">新建</Button>
        <Button type="success" size="small" class="btn-import" style="padding: 2px 7px 1.8px">
          <Upload
            :show-upload-list="false"
            :format="['.xls']"
            action="#">
            <i class="iconfont icon-upload"></i>
            <span>识别导入</span>
          </Upload>
        </Button>
      </div>
    </div>
    <div class="content_box">
      <div class="table-box">
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :height="tableHeight"></Table>
      </div>
      <div class="page_box">
        <Page :total="total" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
      </div>
    </div>
    <div class="extract_box" :class="{addActive:addActive}">
      <extracTableMap ref="extracTableMap" @signOut="signOut"></extracTableMap>
    </div>
    <Modal v-model="modal1"
           title="新增人类活动信息提取任务"
           :mask-closable="false">
      <Form :model="addForm" :label-width="100" ref="addForm" :rules="addFormRules">
        <FormItem prop="name" label="任务名称">
          <Input v-model="addForm.name" placeholder="请填写任务名称，最少四个字符"></Input>
        </FormItem>
        <FormItem prop="url" label="底图服务地址">
          <Input v-model="addForm.url" placeholder="请填写底图服务地址"></Input>
        </FormItem>
        <FormItem label="说明">
          <Input type="textarea" v-model="addForm.remark" placeholder="请填写说明"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="modal1=false">取消</Button>
        <Button type="primary" @click="submitAddForm" :loading="buttonLoading">确认</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { getRemoteList, addRemoteTask, deleteRemoteTask } from '@/api/remoteSensing'
import QueryBox from '@/components/publicQuery'
import extracTableMap from './extracTable_map'
/* 新增的提取任务表格的验证 */
const ADDFORMRULES = {
  name: [
    { required: true, message: '任务名称必须填写', trigger: 'blur' },
    { type: 'string', min: 4, message: '任务名称为4-20个字符组成', trigger: 'blur' },
    { type: 'string', max: 20, message: '任务名称为4-20个字符组成', trigger: 'blur' }
  ],
  url: [
    { required: true, message: '底图服务地址必须填写', trigger: 'blur' }
  ]
}
export default {
  name: 'extracTable',
  data () {
    return {
      /* 查询框查询条件 */
      queryData: {},
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '任务名称',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '说明',
          align: 'center',
          minWidth: 250,
          key: 'remark'
        },
        {
          title: '底图服务',
          align: 'center',
          width: 300,
          key: 'url'
        },
        {
          title: '操作',
          key: 'address',
          width: 240,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.activeId = params.row.id
                    this.openMap()
                  }
                }
              }, '标绘修正'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该项目么？',
                      onOk: () => {
                        this.proDelete(params.row.id)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: [],
      /* 查询框查询条件 */
      obj: {},
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      loading: false,
      /* 提取信息框状态 */
      addActive: false,
      /* 添加提取任务的表单数据 */
      addForm: {
        name: '',
        url: '',
        remark: ''
      },
      /* 添加提取任务的表单验证 */
      addFormRules: ADDFORMRULES,
      /* 用于控制新增核查任务弹框的变量 */
      modal1: false,
      /* 保存解译任务的id */
      activeId: 0,
      /* 新增任务确认按钮的加载状态 */
      buttonLoading: false
    }
  },
  components: {
    QueryBox,
    // 信息提取地图
    extracTableMap
  },
  computed: {
    // 查询表单
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'name',
          title: '任务名称'
        }
      ]
    }
  },
  created () {
    this.getTableList()
  },

  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)

      // this.tableHeight = window.innerHeight - 312
      // this.pageSize = Math.floor((window.innerHeight - 312) / 48)
      this.getTableList()
    }, false)
  },
  watch: {
    queryData () {
      this.getTableList()
    }
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getRemoteList({
        data: {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* 信息提取地图页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    },
    /* 打开新增窗口 */
    openAddModal () {
      this.modal1 = true
      this.$refs.addForm.resetFields()
    },
    /* 打开地图操作 */
    openMap () {
      this.addActive = true
      this.$refs.extracTableMap.initCom()
    },
    /* 保存新增人类活动信息提取的表单上传 */
    submitAddForm () {
      // 打开创建按钮的加载动画
      this.buttonLoading = true
      // 请求加载接口
      addRemoteTask({
        data: {
          name: this.addForm.name,
          url: this.addForm.url,
          remark: this.addForm.remark,
          // todo 用户名需要更换
          createBy: 1 // 用户id
        }
      }).then(res => {
        // 关闭创建按钮的加载动画
        this.buttonLoading = false
        // 判断是否正常
        if (res.data.code === '0000') {
          //  重新加载任务列表数据
          this.getTableList()
          // 成功提示
          this.$Notice.success({ title: '成功创建人类活动信息提取任务' })
          this.modal1 = false
        } else {
          // 错误提示
          this.$Notice.error({
            title: '新增人类活动信息提取任务失败',
            desc: res.data.msg
          })
        }
      })
    },
    /* 删除人类活动信息提取任务 */
    proDelete (id) {
      deleteRemoteTask({
        data: {
          id: [id]
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.getTableList()
          this.$Notice.success({ title: '成功删除人类活动信息提取任务' })
        } else {
          this.$Notice.error({ title: '删除人类活动信息提取任务失败', desc: res.data.msg })
        }
      })
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-upload {
        font-size: 12px;
      }
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      height: calc(100% - 65px);
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
      .table-box {
        height: calc(100% - 60px);
      }
    }
    .extract_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
    .btn-import {
      background-color: rgb(194,146,1);
      border-color: rgb(194,146,1);
      &:hover, &:focus {
        background-color: #B28301;
        border-color: #B28301;
      }
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
  }
</style>
