import config from '../config'
/* 属性操作 */
export default {
  data () {
    return {
      attributeStatus: false,
      attributeObj: {}, // 选中几何的属性详情
      attributeCopy: {}, // 选中几何的属性详情
      attributeEdit: false // 选中几何的属性详情
    }
  },
  filters: {
    nameToAlias (val) {
      for (let item of config.orchardFiled) {
        if (val === item.name) {
          return item.alias
        }
      }
    }
  },
  watch: {
    attributeObj (attr) {
      this.$nextTick(() => {
        // 激活点选的几何,将数据渲染进入表格
        let contentArr = []
        for (let i in attr) {
          contentArr.push(attr[i])
        }
        for (let i in this.$refs.attrKey) {
          this.$refs.attrKey[i].innerText = contentArr[i]
        }
      })
    }
  },
  methods: {
    /* 切换按钮状态 */
    attributeStateToggle () {
      /* 关闭其他工具 */
      this.closeOtherTools('attribute')
      if (!this.attributeStatus) {
        this.attributeStatus = true
        this.attributeObj = {}
      } else {
        this.map.findLayerById('sLayer').removeAll()
        this.attributeStatus = false
      }
    },
    /* 点击列表中删除按钮，删除选中 */
    attrDelchoice (id) {
      this.map.findLayerById('sLayer').removeAll()
      this.originalSelectedGraphics = this.originalSelectedGraphics.filter((res) => res.attributes.ObjectID !== id)
      this.map.findLayerById('sLayer').addMany(this.originalSelectedGraphics)
    },
    /* 鼠标点击选中块高亮，选择层样式高亮，显示属性详情 */
    attrChickList (attr) {
      /* if (!isNaN(attr.area)) {
        attr.area = attr.area + '公顷'
      } */
      // 激活点选的几何
      this.attributeObj = attr
      // 高亮地图上选中的几何
      let gras = []
      for (let item of this.originalSelectedGraphics) {
        let gra = item.clone()
        if (item.attributes.ObjectID === attr.ObjectID) {
          gra.symbol = config.attributeSym
        }
        gras.push(gra)
      }
      this.map.findLayerById('sLayer').removeAll()
      this.map.findLayerById('sLayer').addMany(gras)
    },
    /* 点击复制按钮复制属性 */
    attrCopy (attr) {
      if (JSON.stringify(this.attributeCopy) === '{}') {
        this.attributeCopy = attr
      } else {
        this.attributeCopy = {}
      }
    },
    /* 点击粘贴按钮粘贴属性 */
    async attrPaste (id) {
      if (JSON.stringify(this.attributeCopy) !== '{}') {
        // 获取复制后的激活几何数组
        let arrGras = []
        for (let item of this.originalSelectedGraphics) {
          let gra = item.clone()
          if (item.attributes.ObjectID === id) {
            let attr = JSON.parse(JSON.stringify(this.attributeCopy))
            for (let i in attr) {
              if (i !== 'ObjectID') {
                gra.attributes[i] = attr[i]
              }
            }
          }
          arrGras.push(gra)
        }
        // 将复制后的几何数组替换到选择层
        this.map.findLayerById('sLayer').removeAll()
        this.map.findLayerById('sLayer').addMany(arrGras)
        // 将复制后的几何数组替换回工作层
        this.map.findLayerById('orchardLayer').applyEdits({
          updateFeatures: arrGras
        })
        // 将复制后的几何数组替换选中数组
        this.originalSelectedGraphics = arrGras
        // 激活当前选中的几何
        this.attrChickList(this.attributeObj)
        // 关闭复制
        this.attributeCopy = {}
      }
    },
    /* 点击覆盖按钮，粘贴当前选中的所有几何的属性 */
    attrCover () {
      if (JSON.stringify(this.attributeCopy) !== '{}') {
        // 获取复制后的激活几何数组
        let arrGras = []
        for (let item of this.originalSelectedGraphics) {
          let gra = item.clone()
          let attr = JSON.parse(JSON.stringify(this.attributeCopy))
          for (let i in attr) {
            if (i !== 'ObjectID') {
              gra.attributes[i] = attr[i]
            }
          }
          arrGras.push(gra)
        }
        // 将复制后的几何数组替换到选择层
        this.map.findLayerById('sLayer').removeAll()
        this.map.findLayerById('sLayer').addMany(arrGras)
        // 将复制后的几何数组替换回工作层
        this.map.findLayerById('orchardLayer').applyEdits({
          updateFeatures: arrGras
        })
        // 将复制后的几何数组替换选中数组
        this.originalSelectedGraphics = arrGras
        // 激活当前选中的几何
        this.attrChickList(this.attributeObj)
        // 关闭复制
        this.attributeCopy = {}
      }
    },
    /* 编辑 */
    attrEdit () {
      this.attributeEdit = true
    },
    /* 保存 */
    attrConserve () {
      // 将ref中的值转为属性对象
      console.log(this.$refs.attrKey)
      let arrValue = []
      for (let item of this.$refs.attrKey) {
        arrValue.push(item.innerText)
      }
      let arrKey = []
      for (let i in this.attributeObj) {
        arrKey.push(i)
      }
      let obj = {}
      for (let i in arrKey) {
        obj[arrKey[i]] = arrValue[i]
      }
      // 获取修改后的激活几何数组
      let arrGras = []
      for (let item of this.originalSelectedGraphics) {
        let gra = item.clone()
        if (item.attributes.ObjectID === this.attributeObj.ObjectID) {
          for (let i in obj) {
            if (i !== 'ObjectID') {
              gra.attributes[i] = obj[i]
            }
          }
        }
        arrGras.push(gra)
      }
      // 将复制后的几何数组替换到选择层
      this.map.findLayerById('sLayer').removeAll()
      this.map.findLayerById('sLayer').addMany(arrGras)
      // 将复制后的几何数组替换回工作层
      this.map.findLayerById('orchardLayer').applyEdits({
        updateFeatures: arrGras
      })
      // 将复制后的几何数组替换选中数组
      this.originalSelectedGraphics = arrGras
      // 激活当前选中的几何
      this.attrChickList(this.attributeObj)
      // 关闭编辑
      this.attributeEdit = false
    }
  }
}
